body {
    width: 80vw;
}

h1 {
    text-align: center;
}

div.images {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    border: 15px;
    margin: 30px auto;
}

div.imgWrapper {
    width: 240px;
    aspect-ratio: 1;
}

img {
    cursor: pointer;
    width: 200px;
    aspect-ratio: 1;
}

img:hover {
    transform: scale(1.15);
}

div.overview{
    margin-top: 100px;
}

span{
    font-size: 1.5rem;
}

span.best_score {
    margin-left: 20px;
}

div.game_over {
    font-size: 3rem;
    text-align: center;
}